.screen-size-message {
  display: grid;
  justify-content: center;
  align-items: center;
}
.App {
  font-family: sans-serif;
  text-align: center;
}

.active {
  filter: invert(0%);
}

.inActive {
  filter: invert(50%);
}

:root {
  --color: black;
}

html {
  overflow: hidden;
  width: 100%;
}
body {
  height: 100%;
  position: fixed;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
