@import url("https://fonts.googleapis.com/css2?family=Island+Moments&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Monoton&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bodoni+Moda:opsz,wght@6..96,500&display=swap");

* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

html,
body {
  -webkit-tap-highlight-color: transparent;
  scroll-behavior: smooth;
}

.nav-bar {
  margin: 0.1rem 0.2rem;
  display: flex;
  padding: 13px 10px 5px 10px;
  justify-content: space-between;
  border-radius: 10px;
  background: white;
  position: fixed;
  overflow: hidden;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
  /* box-shadow: 0 1px 1px hsl(0deg 0% 0% / 0.075),
    0 2px 2px hsl(0deg 0% 0% / 0.075), 0 4px 4px hsl(0deg 0% 0% / 0.075),
    0 8px 8px hsl(0deg 0% 0% / 0.075), 0 16px 16px hsl(0deg 0% 0% / 0.075); */
}

body {
  background-color: white;
}

/* Navbar */
.left-navbar {
  width: 15vw;
  height: 100vh;
  /* height: var(--window-Height); */
  background-color: white;
  position: fixed;
  top: 0;
  left: -90vw;
  display: flex;
  flex-direction: column;
  padding: 4rem 0 0 4rem;
  z-index: 10;
  transition: all 0.5s;
}

.left-navbar-change {
  height: 100vh;
  /* height: var(--window-Height); */
  background-color: rgba(255, 255, 255, 0.267);
  position: fixed;
  top: 0;
  display: flex;
  flex-direction: column;
  padding: 2rem 0 4rem 2rem;
  z-index: 10;
  transition: all 0.5s;
  left: 0;
  width: 100%;
}

.section {
  width: 100%;
  /* height: 100vh; */
  height: var(--window-Height);
  border-radius: 10px;
  background-color: #efefef;
  position: relative;
  left: 0;
  transform-origin: left;
  box-shadow: 0.5rem 0.5rem 1rem #aaa;
  padding: 0.1rem 0;
  transition: all 0.5s, transform 0.5s;
  z-index: 12;
  /* position: fixed; */
  /* bottom: calc(10px + (100vh - 100%)); */
}

.section-change {
  width: 100%;
  overflow: hidden;
  /* height: 100vh; */
  border-radius: 10px;
  background-color: #efefef;
  position: relative;
  left: 0;
  /* margin-bottom: 3rem; */
  transform-origin: left;
  box-shadow: 0.5rem 0.5rem 1rem #aaa;
  padding: 0.1rem 0;
  z-index: 12;
  /* height: var(--window-Height); */
  height: 80vh;
  left: 60vw;
  transition: all 0.5s, transform 0.5s;
  transform: rotateY(15deg) translateY(10%);
}

.nav-bar .nav-lines {
  height: 1.15rem;
  margin-top: 0.1rem;
  margin-left: 0.4rem;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

.backArrow {
  width: 23px;
  position: absolute;
  top: 11px;
}
.window .nav-bar .nav-lines > svg {
  width: 23px;
  height: 23px;
  position: absolute;
  top: 13px;
}

.nav-lines div {
  width: 20px;
  height: 2px;
  margin: 2px;
  background: #3c4048;
  transition: width 0.2s;
  box-shadow: 0 3px 4px rgba(0, 0, 0, 0.15);
}

.nav-lines .second {
  width: 15px;
}

.nav-lines .third {
  width: 10px;
}

.nav-lines:hover .second {
  width: 20px;
}

.nav-lines:hover .third {
  width: 20px;
}

.nav-bar .brand {
  width: 3rem;
  margin-bottom: 0.2rem;
  height: 1.75rem;
  overflow: hidden;
  position: relative;
  top: -1px;
}

.brand_logo {
  width: 5em;
  position: absolute;
  top: -1.8rem;
  left: -1.1rem;
  cursor: pointer;
}

.nav-bar .wish-list-cart {
  top: 1px;
  height: 25px;
  border-radius: 50px;
  width: 25px;
  cursor: pointer;
  margin-right: 0.3rem;
  position: relative;
}

.screen-container {
  width: 100vw;
  /* hright: 100vh; */
  min-height: var(--window-Height);
  overflow: hidden;
  /* color: var(--color); */
}

.navbar-link-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-grow: 1;
}

.navbar-link-container .profile {
  color: black;
  display: flex;
  width: 52%;
}

.navbar-link-container .profile .display-icon {
  width: 4rem;
  height: 4rem;
  /* background: red; */
  background-image: var(--profile-image-display);
  background-size: 100% 100%;
  border-radius: 50px;
}

.navbar-link-container .profile .details {
  margin-left: 10px;
  display: grid;
  gap: 1px;
}

.navbar-link-container .profile .name {
  font-size: 1rem;
  font-weight: 900;
}

.navbar-link-container .profile .status {
  font-size: 0.6rem;
  position: relative;
  top: -5px;
}

.navbar-link-container .profile .active-name {
  padding-left: 9px;
  font-size: 0.7rem;
}

.navbar-link-container .profile .icon {
  position: absolute;
  top: 2px;
  font-size: 0.3rem;
}

.navbar-link-container .navbar-link {
  margin: 15px 0;
  display: flex;
  position: relative;
}
.navbar-link-container .navbar-link:active {
  top: 2px;
}

.navbar-link-container .sign-out {
  margin: 15px 0;
  display: flex;
  position: relative;
}

.navbar-link-container .sign-out:active {
  top: 2px;
}

.navbar-link-container .name {
  align-self: center;
  margin-left: 3px;
  color: black;
  margin-bottom: 2px;
}

.navbar-link-container img {
  width: 25px;
  margin-right: 0.5rem;
}

.wish-list-cart .wishlist-icon {
  width: 1.2rem;
  z-index: 1;
  font-size: 24px;
  filter: invert(50%);
}

.wish-list-cart .favourite_svg {
  width: 1.2rem;
  z-index: 1;
  font-size: 24px;
  filter: invert(50%);
  position: absolute;
  top: -13px;
}

.wish-list-cart .delete-icon {
  width: 1.2rem;
  height: 1.4rem;
  z-index: 1;
  position: relative;
  top: -2px;
  filter: invert(20%);
}

.wish-list-cart .delete_svg {
  width: 1.2rem;
  height: 1.4rem;
  z-index: 1;
  position: relative;
  top: -2px;
  filter: invert(20%);
}

