.bottom-bar {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  height: 56px;
  background: transparent;
  overflow: hidden;
  margin: 0.1rem 0.2rem;
  padding: 13px 10px 5px 10px;
  border-radius: 10px;
  z-index: 1;
}

.bottom-bar::before {
  content: "";
  position: absolute;
  top: -32px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 1000px;
  width: 64px;
  height: 64px;
  background: transparent;
  box-shadow: 0px 0px 0px 5000px white;
}

.bottom-round {
  display: flex;
  justify-content: center;
}

.bottom-cart {
  position: fixed;
  bottom: 33px;
  height: 54px;
  width: 54px;
  z-index: 2;
  border-radius: 50px;
  cursor: pointer;
  background: black;
  box-shadow: 0 1px 1px hsl(0deg 0% 0% / 0.075), 0 2px 2px hsl(0deg 0% 0% / 0.075), 0 4px 4px hsl(0deg 0% 0% / 0.075), 0 8px 8px hsl(0deg 0% 0% / 0.075),
    0 16px 16px hsl(0deg 0% 0% / 0.075);
}

.mid-container {
  width: 100vw;
  height: 100%;
  /* height: var(--window-Height); */
  padding: 3rem 0.5rem;
}

.bottom-material-icons {
  width: 2rem;
  position: absolute;
  top: 12px;
  left: 11px;
  z-index: 100;
  font-size: 24px;
  filter: invert(100%);
}

.bottom-count-container {
  position: absolute;
  top: -6px;
  right: -2px;
  margin-left: 10px;
  z-index: 2;
  border-radius: 50%;
  min-width: 23px;
  min-height: 14px;
  background: rgb(235, 68, 68);
  border: 1px solid white;
  justify-content: center;
}

/* .bottom-bar-icons {
  margin-top: 5px;
} */
.bottom-bar-icons {
  margin-top: -6px;
}
.bottom-bar-icons .container {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  justify-items: center;
  gap: 10px;
}

.bottom-bar-icons .container img {
  width: 20px;
}
.bottom-bar-icons .container svg {
  width: 20px;
}

.bottom-bar-icons .container .inActive {
  filter: invert(50%);
}

.bottom-count {
  margin: 2px;
  z-index: 2;
  font-size: 11px;
  display: block;
  text-align: center;
  color: rgb(250, 250, 250);
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  padding: 2px;
  margin-top: 3px;
}
