@import url("https://fonts.googleapis.com/css2?family=Island+Moments&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Monoton&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bodoni+Moda:opsz,wght@6..96,500&display=swap");

* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

.transaction-full-container {
  position: relative;
}

.transaction-full-container label {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
}

.title {
  font-weight: 800;
  font-size: 1.2em;
  letter-spacing: 1px;
  padding: 2rem 0 2.3rem 0;
  font-family: "Roboto", sans-serif;
  text-align: center;
  z-index: 1;
}

.bottom-container {
  background: #fff;
  border-radius: 20px;
  width: 100%;
  margin: auto;
  min-height: 4rem;
  padding: 0.2rem 0.2rem;
}

.bottom-container .item {
  padding: 1rem 1rem;
  border-radius: 15px;
  border-bottom: 1px solid rgba(176, 170, 170, 0.221);
  display: flex;
  justify-content: space-between;
  position: relative;
}
.bottom-container .item:active {
  top: 2px;
}

.bottom-container .item .transaction_id {
  position: relative;
  left: 20px;
  font-weight: 900;
}
.bottom-container .item .amount {
  position: relative;
  left: 38px;
  font-weight: 900;
}

.bottom-container .item .transaction_id_change {
  position: relative;
  left: 15px;
  font-weight: 900;
}
.bottom-container .item .amount_change {
  position: relative;
  left: 28px;
  font-weight: 900;
}
.bottom-container .item .status_change {
  position: relative;
  left: -12px;
  font-weight: 900;
}
.bottom-container .item .status {
  font-weight: 900;
}
.bottom-container .item .date {
  font-weight: 900;
}

.bottom-container .item .name {
  justify-self: start;
  font-weight: 400;
}
.dialog-item {
  padding: 1rem;
}
