.drag-container {
  overflow: hidden;
}
.button-container {
  padding: 2rem 0;
  display: flex;
  min-width: 100%;
  min-height: 5rem;
}
.button-container .custom-btn {
  margin-left: 40px;
  width: 130px;
  height: 40px;
  border-radius: 5px;
  font-family: "Lato", sans-serif;
  font-weight: 500;
  background: white;
  position: relative;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0.5),
    7px 7px 20px 0px rgba(0, 0, 0, 0.1), 4px 4px 5px 0px rgba(0, 0, 0, 0.1);
  outline: none;
}
.custom-btn:active {
  top: 2px;
}

.custom-btn-active {
  margin-left: 40px;
  width: 130px;
  height: 40px;
  border-radius: 5px;
  font-family: "Lato", sans-serif;
  font-weight: 500;
  background: black;
  color: white;
  position: relative;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0.5),
    7px 7px 20px 0px rgba(0, 0, 0, 0.1), 4px 4px 5px 0px rgba(0, 0, 0, 0.1);
  outline: none;
}
