@import url("https://fonts.googleapis.com/css2?family=Island+Moments&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Monoton&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bodoni+Moda:opsz,wght@6..96,500&display=swap");

* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

.full-container {
  position: relative;
}

.container {
  padding-bottom: 5rem;
}

.title {
  font-weight: 800;
  font-size: 1.2em;
  letter-spacing: 1px;
  padding: 2rem 0 2.3rem 0;
  font-family: "Roboto", sans-serif;
  text-align: center;
  z-index: 1;
}

.bottom-container {
  background: #fff;
  border-radius: 20px;
  width: 92%;
  margin: auto;
  min-height: 4rem;
  padding: 0.2rem 0.2rem;
}

.bottom-container .item {
  padding: 1rem 1rem;
  border-radius: 15px;
  border-bottom: 1px solid rgba(176, 170, 170, 0.221);
  position: relative;
}

h1,
h2 {
  font-weight: 800;
  padding-bottom: 0.5rem;
}

p {
  padding-bottom: 0.5rem;
}
