* {
  padding: 0;
  margin: 0;
}
.container {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.brand-name {
  text-align: center;
  margin-top: 30%;
  color: #3c4048;
  font-family: "Sitka Subheading", serif;
  text-transform: uppercase;
  text-shadow: 1px 1px 3px black;
  line-height: 3rem;
}

.brand-name .first {
  font-size: 3rem;
  font-weight: 800;
  letter-spacing: 1px;
}

.brand-name .second {
  margin-top: 1rem;
  font-size: 1.5rem;
  font-weight: 800;
  letter-spacing: 1px;
}

.brand-name .third {
  margin-top: 1rem;
  font-size: 2.5rem;
  font-weight: 800;
  letter-spacing: 1px;
}
