.login-button {
  margin-top: 30%;
}

.anonymus-login-button {
  margin-top: 10%;
}

.copyright {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  font-family: "Roboto", sans-serif;
  padding: 10px 0;
  width: 100%;
  font-size: 0.8rem;
  font-weight: bold;
  letter-spacing: 1px;
  box-shadow: 0 1px 6px rgba(119, 117, 117, 0.15);
}

.brand {
  width: 8rem;
  height: 8rem;
  border-radius: 5000px;
  overflow: hidden;
  margin-top: 3rem;
  box-shadow: 0 10px 300px rgba(180, 178, 178, 0.452);
  display: flex;
  justify-content: center;
  text-align: center;
}

.brand img {
  width: 15rem;
}

.mainLoader {
  position: "absolute";
}
