body {
  margin: 0;
  padding: 0;
  font-family: Arial;
}

.card {
  color: white;
  margin: 20px;
  border: 1px solid lightgray;
  width: 320px;
  height: 180px;
  border-radius: 10px;
  box-shadow: 0 2px 2px 2px rgba(0, 0, 0, 0.05);
  overflow: hidden;
  position: relative;
  background: linear-gradient(45deg, #3023ae, #c86dd7);
}

.card:before {
  content: "";
  display: block;
  height: 320px;
  width: 320px;
  border-radius: 50%;
  margin: 10px 0;
  box-shadow: -4px 2px 10px 5px rgba(255, 255, 255, 0.08) inset;
  position: absolute;
}

.card:after {
  content: "";
  display: block;
  height: 320px;
  width: 320px;
  background-color: transparent;
  border-radius: 50%;
  margin: -240px 100px;
  border: 1px solid white;
  position: absolute;
}

.chip {
  width: 40px;
  height: 40px;
  background: linear-gradient(-60deg, gray, white);
  position: absolute;
  margin: 45px 30px;
  border-radius: 4px;
  overflow: hidden;
}

.chip:before {
  content: "";
  width: 30px;
  height: 20px;
  display: block;
  border: 1px solid gray;
  border-radius: 4px;
  margin: 9px 12px;
  position: absolute;
}

.chip:after {
  content: "";
  width: 30px;
  height: 10px;
  display: block;
  border: 1px solid gray;
  border-radius: 4px;
  margin: 14px -5px;
  position: absolute;
}

.number {
  position: absolute;
  bottom: 55px;
  left: 30px;
  font-size: 18px;
}

.name {
  position: absolute;
  bottom: 25px;
  left: 30px;
  font-size: 14px;
  text-transform: uppercase;
}

.date {
  position: absolute;
  bottom: 25px;
  right: 30px;
  font-size: 14px;
  letter-spacing: 0.5px;
}

.logo {
  width: 80px;
  position: absolute;
  right: 30px;
  top: 20px;
}
