.filter-container {
  width: 100%;
  height: 40px;
  background: rgba(199, 196, 196, 0.084);
  border-radius: 30px;
  display: inline-flex;
  margin-bottom: 2.5rem;
  font-size: 1rem;
  justify-content: space-around;
}

.image-conatiner {
  width: 100%;
  border-radius: 20px;
  position: relative;
}

/* .loading-card {
  width: 10rem;
  height: 10rem;
  background: grey;
} */

.details .round img {
  margin-top: 2px;
  margin-left: 2px;
  width: 1.3rem;
  filter: invert(100%);
}

.details .round svg {
  margin-top: -12px;
  margin-left: 2px;
  width: 1.3rem;
  filter: invert(100%);
}

.wishlist-icon {
  width: 1.3rem;
  position: absolute;
  right: 0.7rem;
  top: -0.7rem;
  z-index: 1;
  /* filter: invert(50%); */
  cursor: pointer;
}

.no-design-found-container {
  display: grid;
  grid-template-columns: 200%;
  width: 100%;
  justify-items: center;
  margin-top: 1rem;
}

.neck-left-window,
.group-left-window,
.double-right-window,
.single-right-window {
  filter: invert(50%);
}
.group-left-window {
  position: relative;
  top: 0;
}
.double-right-window {
  position: relative;
  top: 1px;
}

.second-page .container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  grid-row-gap: 1.8rem;
  margin-bottom: 3.5rem;
}

.second-page .item {
  width: 10.7rem;
  height: 17.1rem;
  background: white;
  text-align: center;
  border-radius: 20px;
  background: transperant;
  border: 2px solid white;
  position: relative;
  box-shadow: 0 3px 4px rgba(0, 0, 0, 0.15);
  cursor: default;
}
.second-page .item .image {
  background: transparent;
  overflow: hidden;
  position: relative;
  left: 2px;
  top: 2px;
  /* border:1px solid black; */
  border-radius: 20px;
  width: 10.1rem;
  height: 13.5rem;
}

.second-page .item .image-conatiner img {
  padding-top: 20px;
  width: 100%;
}
.second-page .item .image-conatiner svg {
  padding-top: 20px;
  width: 100%;
}

.second-page .item .image img {
  /* border:1px solid black; */
  padding-top: 30px;
  width: 100%;
}

.second-page .item .details {
  display: grid;
  grid-template-columns: 1fr 1fr;
  line-height: 1.5rem;
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  position: absolute;
  bottom: 0;
  left: -2px;
}
.second-page .item .details .id {
  grid-column: span 3;
  font-size: 0.6rem;
  letter-spacing: 1px;
  text-shadow: 0px 0px 30px black;
  position: absolute;
  bottom: 1.3rem;
  left: 0.9rem;
}

.second-page .item .details .price {
  display: flex;
  margin: 10px -10px 0 0;
  font-size: 0.8rem;
  position: relative;
  bottom: 14px;
  font-family: "Times New Roman", serif;
  position: absolute;
  bottom: 1.5rem;
}

.second-page .item .details .price .rupee {
  position: absolute;
  top: -4px;
  left: 14px;
  font-size: 0.6rem;
}

.second-page .item .details .price .value {
  position: absolute;
  font-size: 0.9rem;
  top: -3px;
  left: 20px;
}

.second-page .item .round {
  width: 40px;
  height: 40px;
  background: black;
  border-radius: 50px;
  color: white;
  position: absolute;
  left: 140px;
  bottom: 10px;
  text-align: center;
  padding-top: 0.5rem;
  box-shadow: 6px 4px 6px rgba(0, 0, 0, 0.15);
  cursor: pointer;
}

.copyright {
  text-align: center;
  padding: 20px 0;
  width: 100%;
  background-color: #eaeaea;
  font-weight: bold;
  letter-spacing: 1px;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.15);
}
