@import url("https://fonts.googleapis.com/css2?family=Island+Moments&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Monoton&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bodoni+Moda:opsz,wght@6..96,500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

.slide-container {
  display: flex;
  justify-content: center;
  margin: 1rem auto;
}

.child-container {
  min-width: 24rem;
  min-height: 13.5rem;
  display: flex;
  position: relative;
  z-index: 0;
}

.slide-container .child-container > img {
  position: absolute;
  width: 100%;
}

.title {
  font-weight: 800;
  font-size: 1.2em;
  letter-spacing: 1px;
  padding: 20px 0 0 1rem;
  font-family: "Roboto", sans-serif;
  color: black;
}

.most-popular {
  display: flex;
  justify-content: space-between;
  margin: 0 1%;
  padding-bottom: 1.7rem;
}

.most-popular .link {
  margin: 25px 0 0 1rem;
  cursor: pointer;
  font-size: 0.9rem;
  position: relative;
  color: black;
}

.link:active {
  top: 1px;
}
