@import url("https://fonts.googleapis.com/css2?family=Island+Moments&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Monoton&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bodoni+Moda:opsz,wght@6..96,500&display=swap");

html {
  background: #eaeaea;
}

* {
  padding: 0;
  margin: 0;
}

.custom-btn {
  width: 140px;
  height: 40px;
  color: #fff;
  border-radius: 10px;
  padding: 10px 25px;
  font-family: "Roboto", sans-serif;
  font-size: 0.9rem;
  font-weight: 500;
  background: transparent;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0.5), 7px 7px 20px 0px rgba(0, 0, 0, 0.1), 4px 4px 5px 0px rgba(0, 0, 0, 0.1);
  outline: none;
}

/* 16 */
.custom-btn {
  border: 1px solid #eaeaea;
  background: white;
  color: #000;
}

.custom-btn:active {
  top: 2px;
}

.second-page {
  margin: 1rem 0.5rem 0.5rem 0.5rem;
}
.container {
  background: #fff;
  /* background: red; */
  border-radius: 20px;
  width: 100%;
  min-height: 4rem;
  padding: 0.2rem 1px;
  position: relative;
}

.image-conatiner {
  width: 100%;
  border-radius: 20px;
  position: relative;
}

.wishlist-icon {
  width: 1.3rem;
  position: absolute;
  right: 1rem;
  top: 0.8rem;
  /* filter: invert(50%); */
  cursor: pointer;
}

.item {
  color: black;
  cursor: default;
  background-color: #eaeaea;
  width: 92%;
  background: white;
  border-radius: 20px;
  box-shadow: 0 3px 4px rgba(0, 0, 0, 0.15);
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  margin: 0.9rem auto;
  min-height: 5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
}

.second-page .image {
  width: 100%;
  min-height: 16.8rem;
  background: transparent;
  overflow: hidden;
}

.second-page img {
  width: 100%;
  border-radius: 20px;
}

.information-page {
  display: flex;
  justify-content: center;
}

.information-page .container {
  width: 96%;
  border-radius: 20px 20px 0 0;
}

.information-page .container .item .rating {
  margin-bottom: 1.6rem;
}

.information-page .container .ratingCount {
  cursor: pointer;
  margin-left: auto;
  margin-right: 0.5rem;
  align-self: center;
  font-size: 0.7rem;
}

.item .rating .rating-container {
  width: 6.5rem;
  position: relative;
}

.grey-color,
.yellow-color {
  position: absolute;
}
.item .rating .rating-container .grey-color {
  z-index: 1;
  display: flex;
}
.item .rating .rating-container .yellow-color {
  background-color: transparent;
  width: var(--rating-percentage);
  overflow: hidden;
  display: flex;
  z-index: 0;
}

.item .rating .rating-container img {
  width: 1.3rem;
  cursor: pointer;
}

.middle-container {
  display: flex;
  justify-content: center;
}
.middle-container .container {
  width: 96%;
  display: flex;
  justify-content: space-around;
  border-radius: 0 0 20px 20px;
}

.middle-container .container .price {
  display: flex;
  margin: 10px -10px 0 0;
  margin-right: 9rem;
  font-size: 1.5rem;
  position: relative;
}

.middle-container .container .price .rupee {
  position: absolute;
  top: 0;
  left: 8px;
  font-size: 0.9rem;
}

.middle-container .container .price .value {
  position: absolute;
  top: -2px;
  left: 20px;
}
.no-reviews-title {
  margin-top: 1rem;
  font-weight: 800;
  font-size: 1.2em;
  letter-spacing: 1px;
  padding: 20px 0 0 1rem;
  font-family: "Roboto", sans-serif;
  color: black;
  text-align: center;
}

.title {
  margin-top: 1rem;
  font-weight: 800;
  font-size: 1.2em;
  letter-spacing: 1px;
  padding: 20px 0 0 1rem;
  font-family: "Roboto", sans-serif;
  color: black;
}

.customer-reviews {
  margin: 2rem 0;
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
}

.customer-reviews::-webkit-scrollbar {
  display: none;
}

.inner-container {
  width: 98.5%;
  height: 19.5rem;
  border-radius: 20px;
  overflow-y: scroll;
  margin-top: 1px;
  margin-left: 3px;
}

.upload-container {
  display: flex;
  justify-content: center;
  margin-bottom: 4rem;
}

.upload-inner-container {
  background: #fff;
  display: flex;
  justify-content: center;
  border-radius: 10px;
  padding-top: 8px;
  width: 40%;
  height: 3.5rem;
}

.customer-reviews .container {
  background: #fff;
  border-radius: 20px;
  width: 96%;
  height: 20rem;
}
