@import url("https://fonts.googleapis.com/css2?family=Island+Moments&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Monoton&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bodoni+Moda:opsz,wght@6..96,500&display=swap");

html {
  background: #eaeaea;
}

* {
  padding: 0;
  margin: 0;
}
.second-page {
  margin: 1rem 0.5rem 5rem 0.5rem;
}
.container {
  background: #fff;
  border-radius: 20px;
  width: 100%;
  min-height: 4rem;
  padding: 0.2rem 1px;
}
.empty-title {
  display: flex;
  width: 100%;
  justify-content: center;
}
.item {
  color: black;
  cursor: default;
  background-color: #eaeaea;
  width: 92%;
  background: white;
  border-radius: 20px;
  box-shadow: 0 3px 4px rgba(0, 0, 0, 0.15);
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  margin: 0.9rem auto;
  min-height: 5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
}

.second-page .image {
  width: 5.5rem;
  height: 4rem;
  background: transparent;
  overflow: hidden;
}

.second-page img {
  width: 100%;
}

.second-page .details {
  font-size: 0.8rem;
  line-height: 1rem;
  margin-bottom: 1rem;
}

.close-quantity-container {
  display: grid;
  height: 4rem;
  justify-content: flex-end;
  align-content: space-between;
  justify-items: end;
}

.second-page .close {
  font-weight: 300;
  font-size: 0.8rem;
  cursor: pointer;
}

.second-page .quantity {
  font-weight: 300;
  font-size: 0.7rem;
  color: grey;
}

.second-page .details .price {
  display: flex;
  position: relative;
}

.second-page .details .price .rupee {
  position: absolute;
  top: -1px;
  font-size: 0.6rem;
}

.second-page .details .price .value {
  position: absolute;
  left: 8px;
}
