  padding: 3.6rem 0 1rem 0;
} */
.filter-container {
  width: 100%;
  height: 40px;
  background: rgba(199, 196, 196, 0.084);
  border-radius: 30px;
  display: inline-flex;
  margin-bottom: 2.5rem;
  font-size: 1rem;
  justify-content: space-around;
}

.image-conatiner {
  width: 100%;
  border-radius: 20px;
  position: relative;
  z-index: 1;
}

.wishlist-icon {
  width: 1.5rem;
  position: absolute;
  right: 0.5rem;
  top: 0.6rem;
  /* filter: invert(50%); */
  cursor: pointer;
}

.no-design-found-container {
  display: grid;
  grid-template-columns: 100%;
  justify-items: center;
  margin-top: 1rem;
}

.neck-left-window,
.group-left-window,
.double-right-window,
.single-right-window {
  filter: invert(50%);
}
.group-left-window {
  position: relative;
  top: 0;
}
.double-right-window {
  position: relative;
  top: 1px;
}

.details .round img {
  margin-top: -1px;
  margin-left: 1px;
  transform: scale(0.52);
  filter: invert(100%);
}

.details .round svg {
  margin-top: 5px;
  margin-left: 2px;
  transform: scale(0.7);
  filter: invert(100%);
}

.second-page .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-row-gap: 1.8rem;
}

.second-page .item {
  width: 22rem;
  height: 15.5rem;
  background: white;
  text-align: center;
  border-radius: 20px;
  background: transperant;
  border: 2px solid white;
  position: relative;
  box-shadow: 0 3px 4px rgba(0, 0, 0, 0.15);
  cursor: default;
}
.second-page .item .image {
  background: transparent;
  overflow: hidden;
  position: relative;
  left: 6px;
  top: 5px;
  border-radius: 20px;
  width: 21rem;
  height: 13rem;
}

.second-page .item img {
  border-radius: 20px;
  position: relative;
  top: -15px;
  width: 95%;
}

.second-page .item svg {
  border-radius: 20px;
  position: relative;
  top: -15px;
  width: 95%;
}

.second-page .item .details {
  display: grid;
  grid-template-columns: 1fr 1fr;
  line-height: 1.5rem;
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  position: absolute;
  bottom: -2px;
  left: -5px;
}
.second-page .item .details .id {
  grid-column: span 3;
  font-size: 0.6rem;
  letter-spacing: 1px;
  text-shadow: 0px 0px 30px black;
  position: absolute;
  bottom: 1.3rem;
  left: 1.2rem;
}

.second-page .item .details .price {
  display: flex;
  margin: 10px -10px 0 0;
  font-size: 0.8rem;
  position: relative;
  bottom: 14px;
  font-family: "Times New Roman", serif;
  position: absolute;
  bottom: 1.5rem;
}

.second-page .item .details .price .rupee {
  position: absolute;
  top: -4px;
  left: 14px;
  font-size: 0.6rem;
}

.second-page .item .details .price .value {
  position: absolute;
  font-size: 0.9rem;
  top: -3px;
  left: 20px;
}

.second-page .item .round {
  width: 65px;
  height: 65px;
  background: black;
  border-radius: 5000px;
  color: white;
  position: absolute;
  left: 18.8rem;
  bottom: -10px;
  padding-top: 1.2rem;
  padding-left: 0.1rem;
  font-size: 2rem;
  text-align: center;
  box-shadow: 6px 4px 6px rgba(0, 0, 0, 0.15);
  cursor: pointer;
}
