* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

.dialog-container {
  padding: 1rem;
}
.dialog-container > Button {
  margin-bottom: 0.5rem;
}
.dialog-container Button {
  width: 100%;
}

.display-image-editor-container .overlay {
  position: fixed;
  /* top: 50%; */
  bottom: 0;
  width: 96%;
  left: 50%;
  z-index: 2;
  /* right: 0; */
  height: 60%;
  transform: translate(-50%, -50%);
  /* background: rgba(120, 117, 117, 0.126); */
  /* transition: opacity 500ms; */
  /* border-radius: 20px; */
  /* opacity: 1; */
}

.custom-btn {
  width: 130px;
  height: 40px;
  border-radius: 5px;
  font-family: "Lato", sans-serif;
  font-weight: 500;
  background: white;
  position: absolute;
  bottom: -5rem;
  z-index: 1;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0.5), 7px 7px 20px 0px rgba(0, 0, 0, 0.1), 4px 4px 5px 0px rgba(0, 0, 0, 0.1);
  outline: none;
}
.custom-btn:active {
  bottom: -5.1rem;
}

