.full-container {
  position: relative;
}

.title {
  font-weight: 800;
  font-size: 1.2em;
  letter-spacing: 1px;
  padding: 2rem 0 2.3rem 0;
  font-family: "Roboto", sans-serif;
  color: black;
  text-align: center;
  z-index: 1;
}

.profile {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profile .display-icon {
  width: 8rem;
  height: 8rem;
  /* background: white; */
  background-image: var(--profile-image-display);
  background-size: 100% 100%;
  border-radius: 500px;
  animation: ripple 2000ms infinite;
  transform: rotate(-40deg);

  z-index: -1;
  position: absolute;
}

@keyframes ripple {
  0% {
    box-shadow: 3px 3px 0 0px #efefef, 0 0 0 0px #fff, 0 0 0 0px #efefef, 3px 3px 0 0px #fff, 3px 3px 0 0px #efefef, 0 0 0 0px #fff, 0 0 0 0px #efefef, 3px 3px 0 0px #fff;
    /* width: 9rem; */
    /* height: 9rem; */
  }
  100% {
    box-shadow: 0 0 0 30px #efefef, 0 0 0 30px rgba(255, 255, 255, 0), 0 0 0 45px #efefef, 0 0 0 45px rgba(255, 255, 255, 0), 0 0 0 60px #efefef, 0 0 0 60px rgba(255, 255, 255, 0),
      0 0 0 75px #efefef, 0 0 0 75px rgba(255, 255, 255, 0);
  }
}

.profile .details {
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 9rem;
}

.profile .name {
  font-size: 1rem;
  font-weight: 900;
  margin-top: 1.5rem;
}

.profile .status {
  font-size: 0.6rem;
  display: flex;
  margin-top: 0.4rem;
}

.profile .active-name {
  font-size: 0.6rem;
  margin-left: 5px;
}

.profile .icon {
  font-size: 0.3rem;
  align-self: center;
}

.bottom-container {
  margin: 2rem 0 3rem 0;
  background: #fff;
  border-radius: 20px;
  width: 100%;
  min-height: 4rem;
  padding: 0.2rem 1px;
}
.bottom-container .item {
  color: black;
  padding: 1rem 0;
  border-radius: 20px;
  border-bottom: 1px solid rgba(176, 170, 170, 0.221);
  display: grid;
  grid-template-columns: 5rem 1fr 1fr;
  justify-items: center;
  align-items: start;
  cursor: pointer;
  position: relative;
}
.bottom-container .item:active {
  top: 2px;
}
.bottom-container .item img {
  width: 1rem;
  filter: invert(50%);
}

.bottom-container .item .icon {
  align-self: end;
  padding-top: 0.1rem;
}
.bottom-container .item .name {
  justify-self: start;
  font-weight: 400;
}
.bottom-container .item .arrow {
  justify-self: end;
  padding-right: 1rem;
}
