.second-page .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-row-gap: 1.8rem;
  margin-top: 1.5rem;
}

.second-page .item {
  width: 22rem;
  height: 15rem;
  background: white;
  text-align: center;
  border-radius: 20px;
  background: transperant;
  border: 2px solid white;
  position: relative;
  box-shadow: 0 3px 4px rgba(0, 0, 0, 0.15);
  cursor: default;
}

.empty-title {
  display: flex;
  width: 100%;
  justify-content: center;
  height: 90%;
  align-items: center;
}

.second-page .item .image {
  background: transparent;
  overflow: hidden;
  position: relative;
  left: 6px;
  top: 5px;
  border-radius: 20px;
  width: 21rem;
  height: 13rem;
}

.second-page .item img {
  border-radius: 20px;
  position: relative;
  top: -15px;
  width: 100%;
}

.second-page .item .details {
  display: grid;
  grid-template-columns: 1fr 1fr;
  line-height: 1.5rem;
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  position: absolute;
  bottom: -2px;
  left: -5px;
}
.second-page .item .details .id {
  grid-column: span 3;
  font-size: 1rem;
  letter-spacing: 1px;
  text-shadow: 0px 0px 30px black;
  position: absolute;
  bottom: 0.5rem;
  left: 1.2rem;
}

