.second-page .item .round {
  width: 45px;
  height: 45px;
  background: black;
  border-radius: 5000px;
  color: white;
  position: absolute;
  left: 19.4rem;
  bottom: -10px;
  text-align: center;
  box-shadow: 6px 4px 6px rgba(0, 0, 0, 0.15);
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.second-page .item .round > img {
  filter: invert(100%);
  width: 80%;
  position: absolute;
  top: 3px;
}
