@import url("https://fonts.googleapis.com/css2?family=Island+Moments&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Monoton&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bodoni+Moda:opsz,wght@6..96,500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
.first-page {
  margin-top: 1rem;
  margin-bottom: 1.5rem;
}
.first-page .search-form {
  position: relative;
  top: 50%;
  left: 50%;
  width: 300px;
  height: 40px;
  border-radius: 40px;
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15);
  transform: translate(-50%, 50%);
  background: #fff;
  transition: all 0.3s ease;
}
.first-page .search-form:focus {
  box-shadow: 0 3px 4px rgba(0, 0, 0, 0.15);
}

.first-page .search-input {
  position: absolute;
  top: 10px;
  left: 38px;
  font-size: 14px;
  background: none;
  color: #5a6674;
  width: 245px;
  height: 20px;
  border: none;
  appearance: none;
  outline: none;
  padding-left: 5px;
}

.first-page .search-button {
  position: absolute;
  top: 10px;
  left: 15px;
  height: 20px;
  width: 20px;
  padding: 0;
  margin: 0;
  border: none;
  background: none;
  outline: none !important;
  cursor: pointer;
}

.first-page .search-button svg {
  width: 20px;
  height: 20px;
  fill: #5a6674;
  background: white;
}
