@import url("https://fonts.googleapis.com/css2?family=Island+Moments&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Monoton&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bodoni+Moda:opsz,wght@6..96,500&display=swap");

html {
  background: #eaeaea;
}

* {
  padding: 0;
  margin: 0;
}
.second-page {
  margin: 1rem 0.5rem 5rem 0.5rem;
}
.container {
  background: #fff;
  border-radius: 20px;
  width: 100%;
  min-height: 4rem;
  padding: 0.2rem 1px;
}

.empty-title {
  display: flex;
  width: 100%;
  justify-content: center;
}

.item {
  color: black;
  cursor: default;
  background-color: #eaeaea;
  width: 92%;
  background: white;
  border-radius: 20px;
  box-shadow: 0 3px 4px rgba(0, 0, 0, 0.15);
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  margin: 0.9rem auto;
  min-height: 5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
}

.second-page .image {
  width: 5.5rem;
  height: 4rem;
  background: transparent;
  overflow: hidden;
}

.second-page img {
  width: 100%;
}

.second-page .details {
  font-size: 0.8rem;
  line-height: 1rem;
  margin-bottom: 1rem;
}

.close-quantity-container {
  display: grid;
  height: 4rem;
  justify-content: flex-end;
  align-content: space-between;
  justify-items: end;
}

.second-page .close {
  font-weight: 300;
  font-size: 0.8rem;
  cursor: pointer;
}

.second-page .quantity {
  font-weight: 300;
  font-size: 0.7rem;
  color: grey;
}

.second-page .details .price {
  display: flex;
  position: relative;
}

.second-page .details .price .rupee {
  position: absolute;
  top: -1px;
  font-size: 0.6rem;
}

.second-page .details .price .value {
  position: absolute;
  left: 8px;
}

footer {
  position: fixed;
  bottom: -15px;
  width: 97.6%;
}

.bottom-price-cart {
  text-align: center;
  padding: 20px 0;
  background-color: white;
  font-weight: bold;
  letter-spacing: 1px;
  border-radius: 30px 30px 0 0;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.15);
  z-index: 10;
}

.bottom-price-cart .container {
  display: flex;
  justify-content: space-around;
}

.bottom-price-cart .container .price {
  display: flex;
  margin: 10px -10px 0 0;
  margin-right: 9rem;
  font-size: 1.5rem;
  position: relative;
}

.bottom-price-cart .container .price .rupee {
  position: absolute;
  top: 0;
  left: 8px;
  font-size: 0.9rem;
}

.bottom-price-cart .container .price .value {
  position: absolute;
  top: -2px;
  left: 20px;
}

.bottom-price-cart .custom-btn {
  width: 140px;
  height: 40px;
  color: #fff;
  border-radius: 50px;
  padding: 10px 25px;
  font-family: "Lato", sans-serif;
  font-weight: 500;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0.5),
    7px 7px 20px 0px rgba(0, 0, 0, 0.1), 4px 4px 5px 0px rgba(0, 0, 0, 0.1);
  outline: none;
}

/* 16 */
.bottom-price-cart .custom-btn {
  border: 1px solid #eaeaea;
  background: white;
  color: #000;
}

.bottom-price-cart :hover {
  color: #000;
}

.bottom-price-cart .custom-btn:active {
  top: 2px;
}

.sub-total {
  background: #eaeaea;
  width: 84.5%;
  display: flex;
  padding: 10px 0 10px 0;
  font-family: "Roboto", sans-serif;
  text-align: center;
  justify-content: center;
  position: relative;
  border-radius: 50px 50px 0 0;
  margin-left: 6%;
}

.sub-total .name {
  color: grey;
}

.sub-total .price {
  display: flex;
  padding-left: 3px;
}

.sub-total .price .rupee {
  font-size: 0.6rem;
  padding-top: 1px;
}

.sub-total .price .value {
  padding-left: 2px;
}

.signup-container {
  background: #fff;
  /* width: 84.5%; */
  width: 65%;
  display: flex;
  flex-direction: column;
  padding: 15px 0 15px 0;
  font-family: "Roboto", sans-serif;
  text-align: center;
  justify-content: center;
  position: relative;
  border-radius: 10px;
  margin-bottom: 2rem;
  /* margin-left: 8%; */
  margin-left: 15%;
}
