.second-page {
  overflow: hidden;
}

.image-conatiner {
  width: 100%;
  border-radius: 20px;
  position: relative;
}

.wishlist-icon {
  width: 1.3rem;
  position: absolute;
  right: 0.7rem;
  top: -0.7rem;
  z-index: 1;
  /* filter: invert(50%); */
  cursor: pointer;
}
.details .round > img {
  margin-top: -1.4rem;
  margin-left: 1px;
  transform: scale(0.54);
  filter: invert(100%);
}

.details .round > svg {
  margin-top: -0.7rem;
  margin-left: -3px;
  transform: scale(0.465);
  filter: invert(100%);
}

.second-page .container {
  display: flex;
  justify-items: center;
  grid-column-gap: 1.8rem;
  margin-left: 1rem;
}

.second-page .item {
  min-width: 10.7rem;
  min-height: 17.1rem;
  background: white;
  text-align: center;
  border-radius: 20px;
  background: transperant;
  border: 2px solid white;
  position: relative;
  box-shadow: 0 3px 4px rgba(0, 0, 0, 0.15);
  margin-bottom: 1rem;
  cursor: default;
}
.second-page .item .image {
  background: transparent;
  overflow: hidden;
  position: relative;
  left: 2px;
  top: 2px;
  /* border:1px solid black; */
  border-radius: 20px;
  width: 10.1rem;
  height: 13.5rem;
}

.second-page .item .image-conatiner img {
  padding-top: 20px;
  width: 100%;
}

.second-page .item .image-conatiner svg {
  margin-top: 10px;
  width: 100%;
}

.second-page .item .image img {
  /* border:1px solid black; */
  padding-top: 30px;
  width: 100%;
}

.second-page .item .image {
  background: transparent;
  overflow: hidden;
  position: relative;
  left: 2px;
  top: 2px;
  /* border:1px solid black; */
  border-radius: 20px;
  width: 10.1rem;
  height: 13.5rem;
}

.second-page .item img {
  padding-top: 20px;
  width: 100%;
}

.second-page .item .details {
  display: grid;
  grid-template-columns: 1fr 1fr;
  line-height: 1.5rem;
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  position: absolute;
  bottom: 0;
  left: -2px;
}
.second-page .item .details .id {
  grid-column: span 3;
  font-size: 0.6rem;
  letter-spacing: 1px;
  text-shadow: 0px 0px 30px black;
  position: absolute;
  bottom: 1.3rem;
  left: 0.9rem;
}

.second-page .item .details .price {
  display: flex;
  margin: 10px -10px 0 0;
  font-size: 0.8rem;
  position: relative;
  bottom: 14px;
  font-family: "Times New Roman", serif;
  position: absolute;
  bottom: 1.5rem;
}

.second-page .item .details .price .rupee {
  position: absolute;
  top: -4px;
  left: 14px;
  font-size: 0.6rem;
}

.second-page .item .details .price .value {
  position: absolute;
  font-size: 0.9rem;
  top: -3px;
  left: 20px;
}

.second-page .item .round {
  width: 40px;
  height: 40px;
  background: black;
  border-radius: 50px;
  color: white;
  position: absolute;
  left: 140px;
  bottom: 10px;
  text-align: center;
  padding-top: 0.5rem;
  box-shadow: 6px 4px 6px rgba(0, 0, 0, 0.15);
  cursor: pointer;
}
